@import '../style/variables.scss';

main {
    display: flex;

    .filter {
        max-width: 300px;
        min-width: 300px;
        height: 700px;
        background-color: $bgItemColor;
        margin: 25px;
        border: 1px solid $whiteColor;
        border-radius: 25px;

        .resetFilter {
            display: flex;
            justify-content: center;
            margin: 20px 0;

            button {
                font-size: 16px;
                font-weight: 600;
                border-radius: 25px;
                padding: 8px 15px;
                cursor: pointer;
                border: $redColor 3px solid;
            }

            button:hover {
                box-shadow: 0px 0px 5px 2px $redColor;
            }
        }

        h2 {
            margin: 10px 0 10px 25px;
            color: $whiteColor;
        }

        .options {
            display: flex;
            flex-direction: column;
            justify-content: center;

            .option {
                margin: 2px 0 2px 50px;

                input[type='checkbox'] {
                    display: none;
                }

                label {
                    color: $whiteColor;
                    display: inline-block;
                    cursor: pointer;
                    position: relative;
                    padding-left: 25px;
                    margin-right: 15px;
                }

                label:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 1px;
                    width: 12px;
                    height: 12px;
                    border: 2px solid $whiteColor;
                    background-color: $lightBlackColor;
                    border-radius: 10px;
                }

                input[type='checkbox']:checked+label:before {
                    background-color: $redColor;
                }
            }
        }
    }

    .products {
        display: flex;
        flex-wrap: wrap;
        gap: 25px;
        align-items: flex-start;
        margin: 25px 0;

        a {
            text-decoration: none;
            color: $whiteColor;
        }
    }
}